<template>
  <div v-if="newIconBoxes.length" class="carousel slide">
    <div class="carousel-inner">
      <div class="carousel-item"
           v-for="(slides, i) in newIconBoxes" :key="i"
           :class="slideClass[i]">
        <div class="row">
          <div class="col-3"
               v-for="(slide, j) in slides"
               :key="j">
            <IconBox :info="slide" />
          </div>
        </div>
      </div>
    </div>
    <ol class="carousel-indicators">
      <li v-for="i in newIconBoxes.length"
          :key="i"
          :class="{active: slideCount === (i - 1)}"
          @click="slideTo(i - 1)"></li>
    </ol>
  </div>
</template>

<script>
  import { slideShow } from "../helper/extends";
  import IconBox from "./IconBox";
  export default {
    name: 'IconSlide',
    components: {
      IconBox
    },
    extends: slideShow,
    data() {
      return {
        chunkLength: 4,
        iconBoxes: [
          {
            text: '找附近',
            style: {
              backgroundImage: `url(${require('../assets/img/006-direction.png')})`
            }
          },
          {
            text: '找區域',
            style: {
              backgroundImage: `url(${require('../assets/img/007-gps.png')})`
            }
          },
          {
            text: '找品牌',
            style: {
              backgroundImage: `url(${require('../assets/img/005-business.png')})`
            }
          },
          {
            text: '曾點過',
            style: {
              backgroundImage: `url(${require('../assets/img/cutlery.png')})`
            }
          },
          {
            text: '綁卡',
            style: {
              backgroundImage: `url(${require('../assets/img/008-money.png')})`
            }
          },
          {
            text: '優惠券',
            style: {
              backgroundImage: `url(${require('../assets/img/coupon.png')})`
            }
          }
        ],
      }
    },
    computed: {
      newIconBoxes() {
        return this._.chunk(this.iconBoxes, this.chunkLength);
      }
    },
    created() {
      let length = Math.ceil(this.iconBoxes.length / this.chunkLength);
      this.slideInit(length);
    }
  }
</script>

